.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.p-megamenu.p-component.p-megamenu-horizontal {
  display: flex !important;
  background-color: #04c604;
}
.p-megamenu.p-component.p-megamenu-horizontal .p-megamenu-custom {
  margin-left: auto;
}
.p-megamenu.p-component.p-megamenu-horizontal .p-megamenu-root-list {
  margin-left: 350px;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem
  > .p-menuitem-link
  .p-menuitem-text {
  color: white !important;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem
  > .p-menuitem-link
  .p-menuitem-icon {
  color: white !important;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem
  > .p-menuitem-link
  .p-submenu-icon {
  color: white !important;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem:hover
  > .p-menuitem-link
  .p-menuitem-text {
  color: black !important;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem:hover
  > .p-menuitem-link
  .p-menuitem-icon {
  color: black !important;
}
.p-megamenu
  .p-megamenu-root-list
  > .p-menuitem:hover
  > .p-menuitem-link
  .p-submenu-icon {
  color: black !important;
}
.p-megamenu.p-component.p-megamenu-horizontal .p-megamenu-custom img {
  position: absolute;
  left: 50px;
}
